import React from "react";
import Tilty from 'react-tilty';
import logo from './logo.png';
import './Logo.css'

const Logo = () => {
    return (
      <div className='ma4 mt0 mr2'>
       <Tilty style={{margin:'2rem',height:'150px', width:'150px', transformStyle: 'preserve-3d' }}>
        <div 
          className=" tilt br2 shadow-2 pa2" 
          style={{height:'150px', width:'150px', transform: 'translateZ(30px)' }}>
            <img alt="logo" src={logo}></img>
          
          </div>
      </Tilty>
    </div>
    );
}

export default Logo;